
          @import "src/styles/_variables";
          @import "src/styles/_mixins";
          @import "src/styles/_typography";
        

.dca-calculator .form-row {
  display: flex;
  justify-content: space-between;
}

.dca-calculator .form-row .col {
  flex: 1;
  margin-right: 10px;
}

.dca-calculator .form-row .col:last-child {
  margin-right: 0;
}

.dca-calculator .button-group {
  display: flex;
  justify-content: center;
}

.dca-calculator .button-group button {
  margin-right: 10px;
}

.dca-calculator .button-group button:last-child {
  margin-right: 0;
}


.dca-calculator .result .form-row .col {
  flex: 1;
  text-align: center;
}


.result .col h6 {
  display: flex;
  flex-direction: column;
}
